@import url(https://fonts.googleapis.com/css2?family=Krona+One&family=Work+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Work Sans', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.fancy { 
  font-family: 'Krona One', sans-serif !important;
}

.App {
  text-align: center;
  background-color: #f3f3f3;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
    background-color: transparent !important;
}

.card-deck {
    padding: 17px 0 !important;
    width: 100% !important;
}

.projects__card {
    background-color: transparent !important;
    border: none;
    margin: 0 17px !important;
 }


 .projects__card:hover {
   text-decoration: none;
    opacity: 0.7;
 }

 .projects__project-title {
    text-align: left !important;
    text-align: initial !important;
    color: black !important;
    margin-bottom: .35rem !important;
 }

 .projects__project-type {
    text-align: left !important;
    text-align: initial !important;
    color: grey !important;
 }

.projects__card  > .card-body {
    padding: 0.75rem 0 !important;
}

.projects__card  > .card-footer {
    background-color: transparent !important;
    border: none !important;
    text-align: left !important;
    text-align: initial !important;
    padding: 0px !important;
}

.projects__project-skill{
    background-color: #ebebeb;
    color: grey;
    padding: 5px !important;
    margin-right: 5px;
    margin-bottom: 5px;
    font-size: 0.8em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: inline-block;
}

.placeholder {
    opacity: 0;
}

.projects {
    padding-top: 56px;
}
.intro {
    font-size: 2em;
    text-align: left;
    text-align: initial;
    margin: 5rem 0;
}

.home__project-header {
    font-size: 2em;
    text-align: left;
    text-align: initial;
    margin: 5rem 0;
}
.project__name {
    font-size: 4em;
    background-color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 20px;
}


.project__info {
    position: absolute;
    top: 50vh;
    text-align: left;
    text-align: initial;
    width: 50%;
}


.project__description {
    background-color: white;
    margin-bottom: 20px;
    min-height: 100px;
}

.project__link {
    background-color: white;
    cursor: pointer;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.project__secondary-image {
    position: absolute;
    top: 20vh;
    left: 60vw;
    width: 30vw;
}
.contact {
    min-height: 100vh;
    padding-top: 56px;
}

.contact__info {
    font-size: 2em;
    text-align: left;
    text-align: initial;
    margin: 5rem 0;
}
.navbar {
    background-color: transparent !important;
    position: absolute !important;
    width: 100% !important;
}
